import React, { useState, useContext } from "react"
import { fade, makeStyles } from "@material-ui/core/styles"
import AppBar from "@material-ui/core/AppBar"
import Toolbar from "@material-ui/core/Toolbar"
import IconButton from "@material-ui/core/IconButton"
import Typography from "@material-ui/core/Typography"
import InputBase from "@material-ui/core/InputBase"
import MenuItem from "@material-ui/core/MenuItem"
import Menu from "@material-ui/core/Menu"
import MenuIcon from "@material-ui/icons/Menu"
import SearchIcon from "@material-ui/icons/Search"
import AccountCircle from "@material-ui/icons/AccountCircle"
import MailIcon from "@material-ui/icons/Mail"
import NotificationsIcon from "@material-ui/icons/Notifications"
import MoreIcon from "@material-ui/icons/MoreVert"
import FastfoodIcon from "@material-ui/icons/Fastfood"
import SettingsApplicationsIcon from "@material-ui/icons/SettingsApplications"
import { Link } from "react-router-dom"
import ThemeContext from "../context/ThemeContext/ThemeContext"
import Dropdowns from "./Dropdowns/Dropdowns"
import SearchBox from "../components/SearchBox/SearchBox"
import SearchBtn from "../components/Buttons/SearchBtn"
import { createMuiTheme } from "@material-ui/core/styles"
import logo from '../components/logo.jpg'

import Brightness3Icon from "@material-ui/icons/Brightness3"
import Brightness5Icon from "@material-ui/icons/Brightness5"

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuLogo: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: "none",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(3),
      width: "auto",
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 1),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    
  },
  inputRoot: {
    color: "inherit",
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "20ch",
    },
  },
  sectionDesktop: {
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "flex",
    },
  },
  sectionMobile: {
    display: "flex",
    [theme.breakpoints.up("md")]: {
      display: "none",
      
    },
  },
}))

function PrimarySearchAppBar() {
  const classes = useStyles()
  const [anchorEl, setAnchorEl] = React.useState(null)
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null)

  const [search, setSearch] = useState(null)
  const [searchActive, setSearchActive] = useState(null)
  const [filters, setFilters] = useState({
    category: null,
    area: null,
  })

  const context = useContext(ThemeContext)
  const [isPressed, setIsPressed] = useState(false)

  const getSearchValue = (val) => {
    setSearch(val)
    setSearchActive(val)
    setFilters({
      category: null,
      area: null,
    })
    setIsPressed(false)
  }

  const getFilterValue = (cat, area) => {
    setFilters({
      category: cat,
      area: area,
    })
    setSearch(null)
    setSearchActive(null)
    setIsPressed(false)
  }

  const formPressedSubmit = () => {
    setIsPressed(true)
  }

  // mode description
  let mode

  if (context.theme === "light") {
    mode = (
      <React.Fragment>
        <span className="mr-1">
          <Brightness3Icon />
        </span>
        Dark Mode
      </React.Fragment>
    )
  } else if (context.theme === "dark") {
    mode = (
      <React.Fragment>
        <span className="mr-1">
          <Brightness5Icon />
        </span>
        Light Mode
      </React.Fragment>
    )
  }

  const themeChanger = () => {
    if (context.theme === "light") {
      context.updateTheme("dark")
    } else {
      context.updateTheme("light")
    }
  }

  //end of mode description

  //-------//

  const isMenuOpen = Boolean(anchorEl)
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl)

  const handleProfileMenuOpen = (event) => {
    setAnchorEl(event.currentTarget)
  }

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null)
  }

  const handleMenuClose = () => {
    setAnchorEl(null)
    handleMobileMenuClose()
  }

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget)
  }

  const menuId = "primary-search-account-menu"
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    ></Menu>
  )

  const mobileMenuId = "primary-search-account-menu-mobile"
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
      
    >
      {/* dropdown menu item */}
      <MenuItem style={{background:"#68b0ab"}}>
        <Dropdowns filters={getFilterValue} active={filters} />
      </MenuItem>
      {/* mode menu item */}

      

      {/* mode item */}
      <div className="text-md flex-grow text-left m-2 p-1">
        <button
          onClick={themeChanger}
          className="outline-none focus:outline-none border-none "
        >
          {mode}
        </button>
      </div>
      {/* end of the mode item */}      
    </Menu>
  )

  return (
    <div className={classes.grow}>
      <AppBar position="static" style={{ background: "#68b0ab" }}>
        <Toolbar>
          {/* logo */}
          <Typography className={classes.title} variant="h6" noWrap>
            <div className="flex items-center flex-shrink-0 text-white mr-6 ">
              <Link to="/">                
               <img src={logo} alt="food" style={{width:"55px", height:"55px", borderRadius:"15px"}}/>
              </Link>
            </div>
            {/* end of logo */}
          </Typography>
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchBtn
                isPressed={isPressed}
                searchVal={search}
                filterVal={filters}
              />
              
            </div>
            <SearchBox
              placeholder="Searchâ€¦"
              search={getSearchValue}
              isPressed={formPressedSubmit}
              active={searchActive}
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ "aria-label": "search" }}
            />
          </div>
          <button type="submit" onClick={formPressedSubmit} style={{border:"3px"}}>
              <SearchIcon />
          </button>

          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            
            <div aria-label="show 17 new notifications" color="inherit">
              <Dropdowns filters={getFilterValue} active={filters} />
            </div>

            <div className="text-md flex-grow text-right m-4">
              <button
                onClick={themeChanger}
                className="outline-none focus:outline-none border-none "
              >
                {mode}
              </button>
            </div>
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
      {renderMenu}
    </div>
  )
}

export default PrimarySearchAppBar
