import React, { useState, Fragment, useContext } from "react"
import SearchBox from "../SearchBox/SearchBox"
import SearchBtn from "../Buttons/SearchBtn"
import Dropdowns from "../Dropdowns/Dropdowns"
//////////////////// This includes the search box and its button
import Brightness3Icon from "@material-ui/icons/Brightness3"
import Brightness5Icon from "@material-ui/icons/Brightness5"

import ThemeContext from "../../context/ThemeContext/ThemeContext"
import "../../tailwind.generated.css"

function SearchBar(props) {
  const [search, setSearch] = useState(null)
  const [searchActive, setSearchActive] = useState(null)
  const [filters, setFilters] = useState({
    category: null,
    area: null,
  })
  const [isPressed, setIsPressed] = useState(false)
  const context = useContext(ThemeContext)

  const getSearchValue = (val) => {
    setSearch(val)
    setSearchActive(val)

    setFilters({
      category: null,
      area: null,
    })
    setIsPressed(false)
  }

  const getFilterValue = (cat, area) => {
    setFilters({
      category: cat,
      area: area,
    })
    setSearch(null)
    setSearchActive(null)
    setIsPressed(false)
  }

  const formPressedSubmit = () => {
    setIsPressed(true)
  }

  // mode description
  let mode

  if (context.theme === "light") {
    mode = (
      <Fragment>
        <span className="mr-1">
          <Brightness3Icon />
        </span>
        Dark Mode
      </Fragment>
    )
  } else if (context.theme === "dark") {
    mode = (
      <Fragment>
        <span className="mr-1">
          <Brightness5Icon />
        </span>
        Light Mode
      </Fragment>
    )
  }

  

  const themeChanger = () => {
    if (context.theme === "light") {
      context.updateTheme("dark")
    } else {
      context.updateTheme("light")
    }
  }

  //end of mode description

  return (
    <Fragment>
      <div className="flex flex-row width={50%} md:px-16 sm:flex-col justify-center mt-5 mb-8">
        <SearchBox
          search={getSearchValue}
          isPressed={formPressedSubmit}
          active={searchActive}
        />
        <SearchBtn
          isPressed={isPressed}
          searchVal={search}
          filterVal={filters}
        />

        <Dropdowns filters={getFilterValue} active={filters} />
        <div className="text-md flex-grow text-right">
          <button
            onClick={themeChanger}
            className="outline-none focus:outline-none border-none hover:text-copy-secondary "
          >
            {mode}
          </button>
        </div>
      </div>
    </Fragment>
  )
}

export default SearchBar
